import styled from "styled-components";

export const Wrapper = styled.div `
	// max-width: 1100px;
	// background: #ebfeff;
	// border-radius: 10px;
	// border: 2px solid #0085a3;
	// padding: 20px;
	// box-shadow: 0px 5px 10px rgba(0,0,0,0.25);
	// text-align: center;

	// p {
	// 	font-size: 1rem;
	// }
	display: flex;
	flex-direction: column;
	align-items: center;

	.number {
		margin: 0;
	}

	.question {
		width: 600px;
		text-align: center;
		margin: 1em 0 0 0;
	}

	.options {
		display: flex;
		justify-content: center;
		flex-direction: column;
		align-items: center;
	}

	.options > div {
		margin: 0.5em;
	}

	.image {
		width: 500px;
		height: auto;
		margin: -30px;
	}
`

type ButtonWrapperProps = {
	correct: boolean;
	userClicked: boolean;
}

export const ButtonWrapper = styled.div<ButtonWrapperProps>`
	transition: all 0.3s ease;

	:hover {
		opacity: 0.8;
	}

	button {
		cursor: pointer;
		user-select: none;
		// font-size: 0.8rem;
		// width: 100%;
		// height: 40px;
		// margin: 5px 0;
		background: ${({ correct, userClicked }) => 
		correct ? '#383838'
				: !correct && userClicked
				? '#400000'
				: '#000'};
		border: 3px solid #fff;
		box-shadow: 1px 2px 0px rgba(0,0,0,0.1);
		border-radius: 10px;
		color: #fff;
		text-shadow: 0px 1px 0px rgba(0,0,0,0.25);
	}

`